body {
  min-height: 100%;
  height: 100%;
}
.container-wrapper {
  background-color: #fff;
  color: #727171;
}
.container {
  margin: 0 auto;
  width: 1506px;
  height: 100%;
}
.container .container-title {
  width: 100%;
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  padding: 54px 0px;
  box-sizing: border-box;
  color: #727171;
  display: inline-block;
}
.container-pic {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  padding-bottom: 74px;
}
.container-pic .left-pic {
  float: left;
}
.container-pic .left-pic .pics {
  width: 380px;
  height: 672px;
  float: left;
  position: relative;
}
.container-pic .left-pic .pics img {
  height: 100%;
}
.container-pic .left-world {
  display: inline-block;
  width: 365px;
  height: 672px;
  background: #f6f6f6;
  color: #727171;
  position: relative;
  padding: 20px;
}
.container-pic .left-world .triangle_right {
  width: 0;
  height: 0;
  border-width: 18px;
  border-style: solid;
  border-color: transparent #fff transparent transparent;
  position: absolute;
  top: 52px;
  left: -36px;
}
.container-pic .left-world .pic-tit {
  font-size: 24px;
}
.container-pic .left-world .pic-content {
  line-height: 30px;
  font-size: 18px;
  padding-top: 13px;
}
.seperate {
  width: 100%;
  height: 120px;
  background: #cbadd2;
  font-size: 18px;
  color: #763086;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}
.seperate .seperate-lpic {
  width: 106px;
  height: 105px;
  vertical-align: middle;
}
.seperate .seperate-lpic img {
  height: 100%;
}
.seperate .seperate-world {
  margin: 0 60px;
  font-size: 24px;
}
.seperate .seperate-rpic {
  width: 140px;
  height: 118px;
  margin-left: 20px;
  vertical-align: middle;
}
.seperate .seperate-rpic img {
  height: 100%;
}
.map-pic {
  padding-bottom: 90px !important;
}
.gain-container .gain-content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: #7e318e;
  font-size: 20px;
  padding-bottom: 94px;
}
.gain-container .gain-content .section-part {
  width: 753px;
  height: 158px;
  background: #cbadd2;
  line-height: 158px;
  margin-bottom: 8px;
  padding: 0 30px;
  box-sizing: border-box;
  position: relative;
}
.gain-container .gain-content .section-part:last-child {
  margin-bottom: 0;
}
.gain-container .gain-content .section-part .section-triangle {
  width: 0;
  height: 0;
  border-width: 18px;
  border-style: solid;
  border-color: transparent #fff transparent transparent;
  position: absolute;
  top: 65px;
  right: 0;
}
.gain-container .gain-content .section-part p {
  font-size: 24px;
  display: inline-block;
}
.gain-container .gain-content .section-part .seaction-pic {
  width: 94px;
  height: 90px;
  line-height: 83px;
  display: inline-block;
  position: absolute;
  right: 38px;
  top: 36px;
}
.gain-container .gain-content .section-part .seaction-pic img {
  height: 100%;
}
.left-gain {
  float: left;
  box-sizing: border-box;
}
.right-gain {
  float: left;
  margin-left: 8px;
  width: 744px;
  height: 656px;
}
.right-gain img {
  height: 100%;
}
.content-banner {
  height: 790px;
  padding-top: 120px;
}
